import React from 'react';
import { Flex } from 'antd-mobile';
import { getUserIcon, getWechatIcon, getConnectIcon } from '../service/getIconUrl'
export default (props) => {
    return (
        <div className="connect-container">
            <Flex justify="center">
                <div className="inline">
                    <img src={getUserIcon()} />
                    <div style={{textAlign: 'center'}}>{props.user}</div>
                </div>
                <div className="inline">
                    <img src={getConnectIcon()} />
                </div>
                <div className="inline">
                    <img src={getWechatIcon()} />
                    <div style={{textAlign: 'center'}}>{props.wxUser}</div>
                </div>
        </Flex>
        </div>
        
    )
}