import React, { useState, useEffect } from "react";
import AccountConnectWechat from "./components/AccountConnectWechat";
import { Button, WingBlank, WhiteSpace, Toast } from "antd-mobile";
import { setAuthorization } from "@/service/http";
import storage from "@/utils/storage";
export default (props: any) => {
  const [bindingState, setBindingState] = useState(null);
  const DataApiService = props.DataApiService;
  const history = props.history;
  const stateMap = history.location.state;
  const bindUser = props.bindUser;

  const { wxUser, wxCard, userId, clientToken, userToken, username } = stateMap;

  const bindUserHandle = () => {
    //const userId = props.tokenContext.userId;
    //const token = props.tokenContext.client_token;
    setAuthorization({ userId, client_token: clientToken });

    bindUser({ wxUser, wxCard, userId, clientToken })
      .then((res: any) => {
        if (res.code === 0) {
          history.push({
            pathname: "/dashboard",
            search: `wxCard=${wxCard}&wxUser=${wxUser}&clientToken=${clientToken}&userId=${userId}&userToken=${userToken}`
          });
        } else {
          Toast.fail(res.message);
          history.goBack();
        }
      })
      .catch((err: Error) => {
        console.log("error", err);
      });
  };

  const nextHandle = () => {
    setAuthorization({ userToken: userToken });
    history.push({
      pathname: "/dashboard",
      search: `userToken=${userToken}`
    });
  };

  return (
    <div className="userbinding-container">
      <AccountConnectWechat wxUser={wxUser} user={username} />
      <WhiteSpace size="xl" />
      <div style={{ textAlign: "center" }}>是否要与您当前的微信号绑定？</div>
      <WhiteSpace />

      <div style={{ textAlign: "center" }}>绑定后下次即可免密码快捷登录。</div>

      <WhiteSpace size="xl" />
      <div>
        <WingBlank size="lg">
          <Button onClick={bindUserHandle} style={{ backgroundColor: "#2ab6d9" }} type="primary">
            绑定
          </Button>
        </WingBlank>
      </div>
      <WhiteSpace size="xl" />
      <div>
        <WingBlank size="lg">
          <Button onClick={nextHandle} style={{ border: "none" }}>
            下次再说
          </Button>
        </WingBlank>
      </div>
    </div>
  );
};
