import React, { useContext } from 'react';
import UserBinding from './UserBinding';
import bindUser from '@/service/http/authentication/bindUser';
import DataApiService from '@/service/http';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
require('./style.scss');

export default (props: any) => {
    const tokenContext = useContext(TokenContext);
    
    return (
        <UserBinding
            DataApiService={DataApiService}
            tokenContext={tokenContext}
            bindUser={bindUser}
            history={props.history}
        />
    );
};
